.App {
  display: flex;
  min-height: 100vh;
  align-items: center;
  justify-content: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

div span {
  font-size: 100px;
  padding: 30px;
}

.mbutton, .pbutton {
  font-size: xx-large;
  width: 100px;
  height: 100px;
  border-radius: 10px; 
  background-color: #d81e5b;
  color: #eee;
}

.mbutton:hover, .pbutton:hover {
  opacity: 0.9;
}